export default {
	data() {	
		return {
			alls:false,
			data:[],
			img:require('../../../../static/icon/icon/shan.png')
		};
	},
	mounted() {
		//获取购物车
		this.chushihua()
	},
	methods:{
		//初始化
		chushihua:function(){
			uni.request({
				url: this.$store.state.url + 'CRM/ShoppingCarList',
				method: 'GET',
				data: {
					cusId  : this.$store.state.Customercode, //客户id
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					if (JSON.parse(res.data).code == 0 || JSON.parse(res.data).Code == 0) {
						var asd = JSON.parse(res.data).Data.Dtos
						for(var i = 0; i < asd.length; i ++){
							asd[i].bool = false
							if(asd[i].Desc.length > 15){
								asd[i].Desc = asd[i].Desc.substring(0,20)
							}
						}
						this.data = asd
					} else {
						uni.showToast({
							title: JSON.parse(res.data).Data,
							icon: "none"
						})
					}
				}
			});
			
			
		},
		
		
		//删除商品
		dilit:function(item){
			console.log(item.ShoppingCarId)
			uni.request({
			        url: this.$store.state.url + 'CRM/DeleteProductFromCar',
			        method: 'POST',
			        data: {
			            id:item.ShoppingCarId
			        },
			        header: {
			            'Token': this.$store.state.token
			        },
			        success: (res) => {
			            var asd = JSON.parse(res.data).Code 
			            if (asd == 0) {
							this.chushihua()
			            } else {
			                uni.showToast({
			                    title: JSON.parse(res.data).Msg || '错误',
			                    icon: "none"
			                })
			            }
			        }
			    });
			
		},
		all:function(){
			this.alls = !this.alls
			for(var i =0; i < this.data.length; i ++ ){
				this.data[i].bool = this.alls
			}
		},
		//点击勾选
		checkbox(i){
			this.data[i].bool = !this.data[i].bool
			var asd = true
			for(var i = 0; i < this.data.length; i ++){
				if(!this.data[i].bool){
					asd = false
				}
			}
			this.alls = asd
		},
		//下订单
		xiadingdan:function(){
			var asd = []
			for(var i = 0; i < this.data.length; i ++){
				if(this.data[i].bool){
					asd.push(this.data[i])
				}
			}
			this.$store.state.Buy = asd
			console.log(this.$store.state.Buy)
			uni.navigateTo({
			    url: "./Buy",
			});

		}

	}
}
